.heros{
    height: 100vh;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%) ;
    display: flex;
}
.heros_left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:20px;
    padding-left: 180px;
    line-height: 1.1;
}
.heros_left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}
.heros_left p{
    color: #171717;
    font-size: 90px;
    font-weight: 700;


}
.heros-hand-icon{
    display: flex;
    align-items: center;
    gap:20px;

}
.heros-hand-icon img{
    width: 90px;
}

.heros-letest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background-color: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;

}
.heros_right img{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 500px;
     
}