.newsletter{
    width: 65%0;
    height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 50px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%) ;
     gap: 30px;

}
.newsletter h1{
     color: #454545;
     font-size: 55px;
     font-weight: 600;
}
.newsletter p{
    color: #454545;
    font-size: 20px;
}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3; 
}
.newsletter input{
    width: 500px;
    margin-left: 30px;
    outline: none;
    border: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;


}
.newsletter button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;



}